"use client"

import { useState, useEffect } from "react"
import { Card } from "@/components/ui/card"
import { BarChart } from "@/components/ui/bar-chart"

interface EnrollmentTrend {
  month: string;
  enrollments: number;
  completions: number;
}

interface CategoryStats {
  category_id: number;
  category_name: string;
  course_count: number;
  total_students: number;
  avg_completion: number;
}

interface InstructorStats {
  instructor_id: number;
  instructor_name: string;
  course_count: number;
  total_students: number;
  avg_rating: number;
  avg_completion: number;
}

interface ReportData {
  platform: {
    total_courses: number;
    total_students: number;
    total_enrollments: number;
    avg_completion_rate: number;
  };
  trends: {
    enrollments: EnrollmentTrend[];
    completions: {
      month: string;
      total_completions: number;
      avg_completion_days: number;
    }[];
  };
  categories: CategoryStats[];
  instructors: InstructorStats[];
  engagement: {
    total_submissions: number;
    completed_submissions: number;
    avg_score: number;
  };
}

export default function ReportsPage() {
  const [data, setData] = useState<ReportData | null>(null)
  const [loading, setLoading] = useState(true)
  const [dateRange, setDateRange] = useState({
    start: new Date(Date.now() - 30 * 24 * 60 * 60 * 1000).toISOString().split('T')[0],
    end: new Date().toISOString().split('T')[0]
  })

  const fetchReports = async () => {
    try {
      const params = new URLSearchParams({
        start: dateRange.start,
        end: dateRange.end
      })

      const response = await fetch(`/api/reports?${params}`)
      if (!response.ok) {
        throw new Error('Failed to fetch reports')
      }
      const data = await response.json()
      setData(data)
    } catch (error) {
      console.error('Error fetching reports:', error)
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    fetchReports()
  }, [dateRange])

  if (loading) {
    return (
      <div className="p-8">
        <div className="animate-pulse">
          <div className="h-8 bg-gray-200 rounded w-1/4 mb-4"></div>
          <div className="h-4 bg-gray-200 rounded w-1/3 mb-8"></div>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
            {[1, 2, 3, 4].map((i) => (
              <Card key={i} className="p-6">
                <div className="h-6 bg-gray-200 rounded w-1/2 mb-2"></div>
                <div className="h-8 bg-gray-200 rounded w-1/3"></div>
              </Card>
            ))}
          </div>
          <Card className="p-6 mb-8">
            <div className="h-[400px] bg-gray-100 rounded"></div>
          </Card>
        </div>
      </div>
    )
  }

  if (!data) {
    return (
      <div className="p-8">
        <Card className="p-12 text-center">
          <p className="text-lg text-gray-500">Failed to load reports</p>
        </Card>
      </div>
    )
  }

  const enrollmentChartData = {
    labels: data.trends.enrollments.map(trend => trend.month),
    datasets: [
      {
        label: 'Enrollments',
        data: data.trends.enrollments.map(trend => trend.enrollments),
        backgroundColor: 'rgba(59, 130, 246, 0.5)',
      },
      {
        label: 'Completions',
        data: data.trends.enrollments.map(trend => trend.completions),
        backgroundColor: 'rgba(34, 197, 94, 0.5)',
      }
    ]
  }

  const categoryChartData = {
    labels: data.categories.map(cat => cat.category_name),
    datasets: [
      {
        label: 'Total Students',
        data: data.categories.map(cat => cat.total_students),
        backgroundColor: 'rgba(59, 130, 246, 0.5)',
        yAxisID: 'students',
      },
      {
        label: 'Average Completion Rate',
        data: data.categories.map(cat => Math.round(cat.avg_completion || 0)),
        backgroundColor: 'rgba(34, 197, 94, 0.5)',
        yAxisID: 'completion',
      }
    ]
  }

  return (
    <div className="p-8">
      <div className="mb-8">
        <h1 className="text-2xl font-bold mb-2">Analytics Dashboard</h1>
        <p className="text-gray-600">Platform performance and engagement metrics</p>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
        <Card className="p-6">
          <h3 className="text-sm font-medium text-gray-500 mb-1">Total Students</h3>
          <p className="text-3xl font-bold">{data.platform.total_students}</p>
        </Card>

        <Card className="p-6">
          <h3 className="text-sm font-medium text-gray-500 mb-1">Total Courses</h3>
          <p className="text-3xl font-bold">{data.platform.total_courses}</p>
        </Card>

        <Card className="p-6">
          <h3 className="text-sm font-medium text-gray-500 mb-1">Total Enrollments</h3>
          <p className="text-3xl font-bold">{data.platform.total_enrollments}</p>
        </Card>

        <Card className="p-6">
          <h3 className="text-sm font-medium text-gray-500 mb-1">Avg. Completion Rate</h3>
          <p className="text-3xl font-bold">
            {Math.round(data.platform.avg_completion_rate)}%
          </p>
        </Card>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-8 mb-8">
        <Card className="p-6">
          <h2 className="text-lg font-semibold mb-4">Enrollment Trends</h2>
          <div className="h-[400px]">
            <BarChart data={enrollmentChartData} />
          </div>
        </Card>

        <Card className="p-6">
          <h2 className="text-lg font-semibold mb-4">Category Performance</h2>
          <div className="h-[400px]">
            <BarChart 
              data={categoryChartData}
              options={{
                scales: {
                  students: {
                    type: 'linear',
                    position: 'left',
                    title: {
                      display: true,
                      text: 'Total Students'
                    }
                  },
                  completion: {
                    type: 'linear',
                    position: 'right',
                    title: {
                      display: true,
                      text: 'Completion Rate (%)'
                    },
                    grid: {
                      drawOnChartArea: false,
                    },
                  },
                },
              }}
            />
          </div>
        </Card>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
        <Card className="p-6">
          <h2 className="text-lg font-semibold mb-4">Top Performing Instructors</h2>
          <div className="space-y-4">
            {data.instructors
              .sort((a, b) => b.total_students - a.total_students)
              .slice(0, 5)
              .map((instructor) => (
                <div key={instructor.instructor_id} className="flex items-center justify-between">
                  <div>
                    <p className="font-medium">{instructor.instructor_name}</p>
                    <p className="text-sm text-gray-500">
                      {instructor.course_count} courses â€¢ {instructor.total_students} students
                    </p>
                  </div>
                  <div className="text-right">
                    <p className="font-medium">{Math.round(instructor.avg_completion)}%</p>
                    <p className="text-sm text-gray-500">completion rate</p>
                  </div>
                </div>
              ))}
          </div>
        </Card>

        <Card className="p-6">
          <h2 className="text-lg font-semibold mb-4">Student Engagement</h2>
          <div className="space-y-6">
            <div>
              <div className="flex justify-between mb-2">
                <span className="text-gray-500">Assignment Completion Rate</span>
                <span className="font-medium">
                  {Math.round((data.engagement.completed_submissions / data.engagement.total_submissions) * 100)}%
                </span>
              </div>
              <div className="w-full bg-gray-200 rounded-full h-2.5">
                <div
                  className="bg-blue-600 h-2.5 rounded-full"
                  style={{
                    width: `${(data.engagement.completed_submissions / data.engagement.total_submissions) * 100}%`
                  }}
                />
              </div>
            </div>

            <div>
              <div className="flex justify-between mb-2">
                <span className="text-gray-500">Average Assignment Score</span>
                <span className="font-medium">
                  {Math.round(data.engagement.avg_score)}%
                </span>
              </div>
              <div className="w-full bg-gray-200 rounded-full h-2.5">
                <div
                  className="bg-green-600 h-2.5 rounded-full"
                  style={{
                    width: `${data.engagement.avg_score}%`
                  }}
                />
              </div>
            </div>
          </div>
        </Card>
      </div>
    </div>
  )
}
